  padding-top: 14px;
  margin-bottom: 14px;
}
button{
  font-size: 14px;
  background-color: white;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
button:hover{
  background: lightgreen;
}
button:focus{
  outline: none;
}
.active{
  background: lightgreen
}
.fade-enter-active{
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
.fade-leave-active{
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
.fade-enter{
  opacity: 0;
}
